<script lang="coffeescript">

  
  # todo: padding right

  export style = undefined
  export offset = 0
  export ellipsisActive = false

  parentWidth = undefined 
  childWidth = undefined
  


  `$: ellipsisActive = ((childWidth + parseInt(offset)) > parentWidth)`

</script>



<div container {style} ellipsis={ellipsisActive} {childWidth} {parentWidth} bind:clientWidth={parentWidth}>
  <span subcontainer bind:offsetWidth={childWidth}>
    <slot/>
  </span>
</div>

<style lang="sass">
  [container]
    max-width: calc(100% - 1px) // force a pixel width
    width: calc(100% - 1px) // force a pixel width
    white-space: nowrap
    overflow: hidden
    text-overflow: ellipsis
    display: block
  [subcontainer]
    max-width: calc(100% - 1px) // force a pixel width
    width: calc(100% - 1px) // force a pixel width
</style>